html, body, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flip-img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
